.list {
  width: 100%;
  list-style: none;
  padding: 0 50px;
  box-sizing: border-box;
  margin: 24px 0 67px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 20px 20px;
}

@media screen and (max-width: 759px) {
  .list {
    padding: 0 10px;
  }
}